.users-wrap {
  display: flex;
  margin: 15px;
  justify-content: center;
  flex: 1;

  .segment {
    flex: 1;
    min-width: 525px;
    margin: 0;
    display: flex;
    flex-direction: column;


    @media only screen and (max-width: 767px) {
      min-width: 280px;
      thead {
        display: none !important;
      }

      tbody {
        tr {
          position: relative;

          td {
            text-align: left !important;
          }
        }
      }

    }

    .ttl-wrap {
      .icon {
        cursor: pointer;
      }
    }

    .tbl-wrap {
      overflow: auto;
    }

    .status {
      font-weight: bold;

      &.done:before {
        color: greenyellow;
        content: "Done ";
      }

      &.in-progress:before {
        color: #fbbd08;
        content: "InProgress";
      }

      &.unknown:before {
        color: #ff5f5f;
        content: "UnKnown";
      }

      &.waiting:before {
        color: #ff5f5f;
        content: "Wating";
      }

      &.idle:before {
        color: #629dff;
        content: "Idle";
      }


    }

    tr {
      cursor: pointer;

      th{
        &.weekly-summary{
          $WP: 200px;
          min-width: $WP;
          max-width: $WP;
          width: $WP;
        }
        &.weekly-progress{

        }
        &.last-update{
          $WP: 150px;
          min-width: $WP;
          max-width: $WP;
          width: $WP;
        }
      }

      &.selected {

        td {
          position: relative;
          color: #e9bcff;
          z-index: 2;
          text-overflow: ellipsis;
          overflow: hidden;
          $borderColor: #8d4ffc;

          &:first-child {
            &:after {
              border-left: 5px solid $borderColor;
              content: '';
              position: absolute;
              height: 100%;
              left: 0;
              top: 0;
            }

            &.multi-select:hover {
              background: rgba(135, 75, 255, 0.3) !important;
            }

            background: rgba(135, 75, 255, 0.3);
          }

        }
      }

      td.selectable {
        $w: 30px;
        width: $w;
        min-width: $w;
        max-width: $w;

        .checkbox {
          display: flex;
          height: 100%;
        }

        input, label {
          opacity: 0 !important;
          flex: 1;
        }
      }

      td {
        height: 40px;


        .ui.indicating.progress {
          margin-bottom: 0;

          .bar {
            &:after {
              background: #000;
            }

            & ~ .label {
              font-size: 10px;
              font-weight: bold;
              left: 6px;
              top: 6.5px;
              background: #383737;
              height: 12px;
              padding: 5px 3px;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 5px;
              width: auto;
              margin: 0;
            }

            .progress {
              font-size: 10px;
              font-weight: bold;
              color: #000;
            }
          }
        }

        //['#adff2f', '#c4e83e', '#d5d148', '#e3b850', '#ee9e56', '#f8825b', '#ff5f5f']



        .ui.indicating.progress[data-percent^="3"] .bar {
          background-color: #f8825b;

          & ~ .label {
            color: #f8825b
          }
        }

        .ui.indicating.progress[data-percent^="4"] .bar, .ui.indicating.progress[data-percent^="5"] .bar {
          background-color: #ee9e56;

          & ~ .label {
            color: #ee9e56
          }

        }

        .ui.indicating.progress[data-percent^="6"] .bar {
          background-color: #e3b850;

          & ~ .label {
            color: #e3b850
          }
        }

        .ui.indicating.progress[data-percent^="7"] .bar, .ui.indicating.progress[data-percent^="8"] .bar {
          background-color: #d5d148;

          & ~ .label {
            color: #d5d148
          }

        }

        .ui.indicating.progress[data-percent^="9"] .bar, .ui.indicating.progress[data-percent^="10"] .bar {
          background-color: #c4e83e;

          & ~ .label {
            color: #c4e83e
          }
        }

        .ui.progress.success .bar {
          background-color: #71ba00 !important;

          & ~ .label {
            color: #71ba00 !important;
          }
        }

        .ui.indicating.progress[data-percent="0"] .bar,
        .ui.indicating.progress[data-percent="1"] .bar,
        .ui.indicating.progress[data-percent="2"] .bar,
        .ui.indicating.progress[data-percent="3"] .bar,
        .ui.indicating.progress[data-percent="4"] .bar,
        .ui.indicating.progress[data-percent="5"] .bar,
        .ui.indicating.progress[data-percent="6"] .bar,
        .ui.indicating.progress[data-percent="7"] .bar,
        .ui.indicating.progress[data-percent="8"] .bar,
        .ui.indicating.progress[data-percent="9"] .bar,
        .ui.indicating.progress[data-percent^="1"] .bar,
        .ui.indicating.progress[data-percent^="2"] .bar {
          background-color: #ff5f5f;

          & ~ .label {
            color: #ff5f5f;
          }
        }
      }

      .win {
        color: var(--green-dark) !important;
        font-weight: bold;
      }

      .lose {
        color: var(--red-dark) !important;
        font-weight: bold;
      }
    }
  }
}
